.App {
  text-align: center;
  background-color: #111111;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

.btnContainer {
  display: flex;
  flex-direction: column;
  background-color: black;
  width: 100%;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}

.btn:last-child {
  margin-top: 0;
}

.btn {
  all: unset;
  cursor: pointer;
  padding: 10px;
  border-radius: 10px;
  background-color: #111111;
  transition: 50ms;
  margin: 20px;
}

.btn:hover {
  background-color: white;
  color: black;
  transition: 50ms;
}

.secondCheapestContainer {
  display: flex;
  margin-top: 10px;
}

.secondCheapestContainer > p {
  margin: 20px;
  width: 66%;
  background-color: #111111;
  padding: 10px;
  border-radius: 10px;
  font-size: 15px;
}

.App > h1 {
  color: white;
  margin-top: 30px;
  margin-bottom: 10px;
}

.entryContainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  padding: 20px;
}

.entry {
  width: 350px;
  align-items: center;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  margin: 10px;
  border-radius: 10px;
  color: white;
  text-decoration: none;
}

.entry > img {
  margin: 20px;
  transition: 200ms;
}

.entry > img:hover {
  scale: 1.1;
  rotate: 5deg;
  transition: 200ms;
}

.entry > h2 {
  text-wrap: balance;
}

.entry > p {
  width: 66%;
  background-color: black;
  padding: 10px;
  border-radius: 10px;
  font-size: 20px;
  margin-bottom: 20px;
  margin-top: 0;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
